import Avatar from 'components/Avatar';
import { useEffect, useState } from 'react';
import { Profile as ProfileType } from 'types/configs';

interface Props extends ProfileType {
  moduleClasses: (classes: string) => string;
}

const Profile = ({ name, avatar, friends, initialFriends, moduleClasses }: Props) => {
  const [displayFriends, setDisplayFriends] = useState(friends);
  const [previousFriends, setPreviousFriends] = useState(friends);

  useEffect(() => {
    if (displayFriends <= friends) {
      setDisplayFriends(friends);
      setPreviousFriends(friends);
      return;
    }

    const updateStep = Math.ceil((previousFriends - friends) / 40);

    const timeout = setTimeout(() => {
      setDisplayFriends(displayFriends - updateStep);
    }, 50);

    return () => clearTimeout(timeout);
  }, [displayFriends, friends, previousFriends]);

  const friendsPercent = Math.ceil((displayFriends / (!initialFriends ? 1 : initialFriends)) * 100);
  const friendsColor = friendsPercent < 20 ? 'red' : friendsPercent < 50 ? 'orange' : 'limegreen';

  return (
    <>
      <div className={moduleClasses('item photo')}>
        <Avatar avatar={avatar} />
      </div>
      <div className={moduleClasses('item account')}>
        <p title={name}>{name}</p>
      </div>
      <div
        className={moduleClasses('item friends')}
        style={{
          color: friendsColor === 'limegreen' ? '' : friendsColor,
        }}
      >
        <span>
          <b>{displayFriends}</b> friends
        </span>
      </div>
      <div className={moduleClasses('item followers')} style={{ borderColor: friendsColor }}>
        <div
          style={{
            width: `${friendsPercent}%`,
            background: friendsColor,
          }}
        >
          &nbsp;
        </div>
      </div>
    </>
  );
};

export default Profile;
