import { log } from 'utils';
import styles from './style.module.css';
// See: https://github.com/facebook/create-react-app/issues/11756#issuecomment-1422741289
import { randomBytes } from 'crypto';
import { PropsWithChildren } from 'react';

// See: https://stackoverflow.com/a/42321673/192331
const getRandomIntInclusive = (min: number, max: number) => {
  const randomBuffer = new Uint32Array(1);
  window.crypto.getRandomValues(randomBuffer);

  const randomNumber = randomBuffer[0] / (0xffffffff + 1);

  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(randomNumber * (max - min + 1)) + min;
};

const Email = ({ children }: PropsWithChildren) => {
  const email = children ? children.toString() : '';
  if (email.length < 3) {
    return <></>;
  }

  const len = email.length;
  const half = Math.round((email.length - 1) / 2);
  const splitPos1 = getRandomIntInclusive(1, half);
  const splitPos2 = getRandomIntInclusive(half, email.length - 1);

  const part1 = email.slice(0, splitPos1);
  const part2 = email.slice(splitPos1, splitPos2);
  const part3 = email.slice(splitPos2);

  const check = `${part1}${part2}${part3}`;

  const rndStr1 = randomBytes(half / 2).toString('hex');
  const rndStr2 = randomBytes(half / 2).toString('hex');
  log({ email, len, half, splitPos1, splitPos2, part1, part2, part3, check, rndStr1, rndStr2 });

  return (
    <span className={styles.email}>
      <span>{part1}</span>
      <span>{rndStr1}</span>
      <span>{part2}</span>
      <span>{rndStr2}</span>
      <span>{part3}</span>
    </span>
  );
};

export default Email;
